
import React from 'react';
import { getProductPageData } from '@/services/productService';
import { useQuoteStore } from '@/lib/quote-store';
import { Check, ShieldCheck, Factory, Share2, Truck, Star, AlertCircle } from 'lucide-react';
import Link from 'next/link';
import { notFound } from 'next/navigation';
import Image from 'next/image';
import { findImage } from '@/lib/placeholder-images';
import { Button } from '@/components/ui/button';
import { AnimatedDiv } from '@/components/animated-div';
import { Section } from '@/components/ui/section';
import { CTASection } from '@/components/cta-section';
import { Product, Brand, Category, Company } from '@/lib/schema';
import ProductDetailClient from './client-page';

interface PageProps {
  params: { slug: string };
}

export default async function ProductDetailPage({ params }: PageProps) {
  const data = await getProductPageData(params.slug);

  if (!data) {
    return notFound();
  }

  return <ProductDetailClient {...data} />;
}
